@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
    height: 100%;
    font-size: var(--font-default-mobile);
    margin:10px;    
    }

    
    body{
    margin: auto;
    display: grid;
    grid-template-rows: var(--header-height) 1fr  ;
    font-family: 'Montserrat', sans-serif;
   
    }

.App{
    box-shadow: var(--shadow-pop);
    
}

    h2{
        padding:0;
        margin:0;
    }
    

header{
background-color: var(--orange)  ;
color: white;

height: var(--header-height);
width: 100%;

border-radius: 8px 8px 0px 0px;

display: grid;
grid-template-columns: 20% 1fr;

-webkit-align-items: center;

        align-items: center;
-webkit-align-content: center;
        align-content: center;

font-weight: 600;

box-shadow: var(--shadow-pop);
}

header img{
    height: 40%;
    margin-left: 12px;;
}

main{
    background-color: white;   
    width: 100%;

    display: grid;
    justify-items: center;
  
    grid-gap: 1vh;
    grid-template-rows: auto 1fr ;
}


.container-search{
    
    width: 100%;
    height: 100%;  
    margin : 20px 0px 10px 0;
     
}

.container-results{    
    margin:0;
    padding:0;

    width: 100%;
    height: 100%;  

    display:-webkit-flex;  

    display:flex ;
    -webkit-flex-direction: column;
            flex-direction: column;  
    -webkit-justify-content: flex-start;  
            justify-content: flex-start; 
    -webkit-align-items: center; 
            align-items: center;    

    border-radius: 8px ;
    box-shadow: var(--shadow-pop);
}





ol,ul{
    width: 100%;
    padding: 0;
    margin:0;
    font-size:10px;
}

ul , ol li{
    background-color: rgba(201, 201, 201,0.5);   
    
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 30%;
    

    -webkit-align-items: center;
    

            align-items: center;
    text-align: center;    

    list-style-type: none;
    
}

ul{
    height: 30px;     
    font-weight: bold;
}

ol li{
    background-color:white;  
    
    font-size: 10px;
    display: grid;   
}

#results-title{
    width: 100%;     
   
    margin-left:25px; 
    font-size: 12px;
    color:rgb(180, 180, 180);
}
#results-notfound{
    font-size: 14px;
    color:rgb(180, 180, 180);
}



#results-footer{
    width: 100%;
    color: white;
    height:10px;   
     
}


#search-bar{
    display: -webkit-flex;
    display: flex;  
    margin:0 7px;      

}


#search-bar input{    

    margin: 0px 5px;
       border: none;
       border-radius: 5px;
       box-shadow: inset 0 0 4px 0px hsla(0,0%,0%,0.08);

}
::-webkit-input-placeholder{
    color: rgb(201, 201, 201);

}
:-ms-input-placeholder{
    color: rgb(201, 201, 201);

}
::placeholder{
    color: rgb(201, 201, 201);

}
#search-area{
    width: 100%;
    
}

#search-button{
    background-color: var(--blue);
    color:white;    
    padding:7px 12px;

    font-weight: 600;
}

/* --- Tablets -- */
@media screen and (min-height: 550px){

    html{
        font-size: 20px;
    }
        
}

/* --- Desktops -- */
@media screen and ( min-height: 750px){

    html{
        font-size: 25px;
        
              
      

    }
    body{
        max-width:800px;
       
        }
        
}
:root {
    --header-height: 10vh;
    --font-default-mobile: 18px;
    --font-default-laptop: 16px;
    --blue: rgb(74, 121, 182);
    --green: rgb(139, 201, 163);
    --red: rgb(201, 139, 139);
    --orange: rgb(218, 151, 50);
    --shadow-pop: 0 2px 6px 0 hsla(0, 0%, 0%, 0.2);
}
